body,
html {
    padding: 0;
    margin: 0;
}
[contenteditable="true"]:focus,
button:focus,
input:focus,
textarea:focus {
    outline: 0;
}
textarea {
    resize: none;
}
.tl_page_wrap {
    max-width: 732px;
    margin: 0 auto;
}
.tl_page {
    position: relative;
    padding: 21px 0;
}
.button {
    font-family: "Lucida Grande", Arial, sans-serif;
    font-weight: 600;
    font-style: normal;
    font-size: 17px;
    color: #000;
    text-decoration: none;
    border: 2px solid #333;
    border-radius: 16px;
    text-transform: uppercase;
    padding: 4px 12px;
    margin: 0 0 15px;
    background-color: #fff;
    cursor: pointer;
}
.tl_message {
    font-family: "Lucida Grande", Arial, sans-serif;
    padding: 70px 0;
    color: #79828b;
    text-align: center;
}
.tl_message h1 {
    font-size: 120px;
    margin: 0;
}
.tl_message_buttons {
    padding: 70px 0;
}
.tl_message_buttons .button {
    display: inline-block;
    margin: 0;
}
.tl_message_buttons .button + .button {
    margin-left: 12px;
}
.tl_article_buttons {
    position: fixed;
    left: 50%;
    top: 0;
    right: 0;
    margin: 46px 0 0 376px;
}
@media (max-width: 960px) {
    .tl_article_buttons {
        position: static;
        width: auto;
        margin: 30px 21px;
        padding: 1px 0;
    }
}
.tl_article_buttons .button {
    transition: opacity 0.4s ease;
    visibility: hidden;
    opacity: 0;
}
.tl_article_buttons .edit_button {
    position: absolute;
}
.tl_article_edit .publish_button,
.tl_article_saving .publish_button {
    visibility: visible;
    opacity: 1;
}
.tl_article_saving .publish_button {
    opacity: 0.4;
}
.tl_article .share_button,
.tl_article_editable .edit_button {
    visibility: visible;
    opacity: 1;
}
.tl_article_edit .edit_button,
.tl_article_edit .share_button,
.tl_article_saving .edit_button,
.tl_article_saving .share_button {
    visibility: hidden;
    opacity: 0;
}
.tl_article .ql-container {
    height: auto;
}
.prompt {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.prompt .prompt_input_wrap {
    overflow: hidden;
}
.prompt .prompt_input {
    width: 100%;
    margin: 0;
    border: none;
    background-color: transparent;
    box-sizing: border-box;
}
.tl_tooltip {
    position: absolute;
    left: 0;
    top: 0;
    background-color: #292927;
    border-radius: 6px;
    padding: 9px 12px;
    line-height: 0;
    white-space: nowrap;
    pointer-events: none;
    transition: opacity 0.2s ease 0.06s, transform 0.2s ease 0.06s;
    transform: translate3d(0, 16px, 0);
    opacity: 0;
}
.tl_tooltip.bottom {
    transform: translate3d(0, -16px, 0);
}
.tl_tooltip.bottom button {
    transform: translate3d(0, -10px, 0);
    opacity: 1;
}
.tl_tooltip.move_anim {
    transition: opacity 0.2s ease 0.06s, transform 0.2s ease 0.06s, top 0.2s ease, left 0.2s ease;
}
.tl_tooltip.shown {
    pointer-events: auto;
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
.tl_tooltip.shown button {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
.tl_tooltip.shown button.disabled {
    cursor: default;
    opacity: 0.6;
}
.tl_tooltip .button_hover,
.tl_tooltip button {
    display: inline-block;
    background-color: transparent;
    padding: 0;
    margin: 0;
    border-radius: 3px;
    border: none;
    cursor: pointer;
}
.tl_tooltip button {
    position: relative;
    transition: opacity 0.2s ease, transform 0.2s ease, background-color 0.2s ease;
    transform: translate3d(0, 10px, 0);
    opacity: 0;
}
.tl_tooltip button:after,
.tl_tooltip button:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: opacity 0.2s ease, transform 0.2s ease;
}
.tl_tooltip button.active:after,
.tl_tooltip button:before {
    transform: scale3d(1, 1, 1);
    opacity: 1;
}
.tl_tooltip button.active:before,
.tl_tooltip button:after {
    transform: scale3d(1.2, 1.2, 1);
    opacity: 0;
}
.tl_tooltip button#_bold_button {
    transition-delay: 0s;
}
.tl_tooltip button#_italic_button {
    transition-delay: 0.02s;
}
.tl_tooltip button#_link_button {
    transition-delay: 0.04s;
}
.tl_tooltip button#_header_button {
    transition-delay: 0.06s;
}
.tl_tooltip button#_subheader_button {
    transition-delay: 0.08s;
}
.tl_tooltip button#_quote_button {
    transition-delay: 0.1s;
}
.tl_tooltip button + button {
    margin-left: 7px;
}
.tl_tooltip button.active {
    background-color: #eaeaea;
}
.tl_tooltip .button_hover {
    background-color: #545452;
}
.tl_tooltip .buttons,
.tl_tooltip .prompt {
    transition: opacity 0.2s ease;
}
.tl_tooltip .prompt,
.tl_tooltip.tooltip_prompt .buttons {
    pointer-events: none;
    opacity: 0;
}
.tl_tooltip.shown.buttons,
.tl_tooltip.shown.tooltip_prompt .prompt {
    pointer-events: auto;
    opacity: 1;
}
.tl_tooltip .prompt_input {
    font-family: "Lucida Grande", Arial, sans-serif;
    font-size: 15px;
    line-height: 18px;
    color: #fff;
    padding: 13px 10px;
}
.tl_tooltip .close {
    float: right;
    width: 18px;
    font-size: 15px;
    line-height: 18px;
    margin: 13px 5px;
    color: #ccc;
    text-align: center;
    cursor: pointer;
    transition: color 0.2s ease;
}
.tl_tooltip .close:before {
    content: "\00d7";
}
.tl_tooltip .close:hover {
    color: #fff;
}
.button_hover {
    position: absolute;
    transition: opacity 0.2s ease;
    opacity: 0;
}
.tl_tooltip.hover_anim .button_hover {
    transition: opacity 0.2s ease, transform 0.2s ease;
}
.tl_tooltip.hover .button_hover {
    opacity: 1;
}
.tl_tooltip[data-hover="_bold_button"] .button_hover {
    transform: translate3d(0, 0, 0);
}
.tl_tooltip[data-hover="_italic_button"] .button_hover {
    transform: translate3d(33px, 0, 0);
}
.tl_tooltip[data-hover="_link_button"] .button_hover {
    transform: translate3d(66px, 0, 0);
}
.tl_tooltip[data-hover="_header_button"] .button_hover {
    transform: translate3d(106px, 0, 0);
}
.tl_tooltip[data-hover="_subheader_button"] .button_hover {
    transform: translate3d(139px, 0, 0);
}
.tl_tooltip[data-hover="_quote_button"] .button_hover {
    transform: translate3d(172px, 0, 0);
}
body.mobile .button_hover {
    display: none;
}
.buttons {
    position: relative;
}
